@charset "UTF-8";
/* リキッドレイアウト対応 */
:root {
  --padding-pc: 25px;
  --padding-sp: 20px;
  --base-font-family: "Gothic A1", sans-serif;
  --second-font-family: "Shippori Mincho", serif;
  --zen-old-mincho: "Zen Old Mincho", serif;
  --darumadrop-one: "Darumadrop One", cursive;
  --regular: 400;
  --medium: 500;
  --bold: 700;
  --black: #000;
  --white: #fff;
  --green: #6d8e1b;
}

body {
  font-family: var(--base-font-family);
}

@media only screen and (min-width: 768px) {
  .sp {
    display: none !important;
  }
}
@media only screen and (max-width: 767px) {
  .pc {
    display: none !important;
  }
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
html {
  font-size: 16px;
}
@media (min-width: 768px) {
  html {
    font-size: 0.8333333333vw;
  }
}
@media (min-width: 1920px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
a[href^="tel:"] {
  pointer-events: none;
}
@media screen and (max-width: 767px) {
  a[href^="tel:"] {
    pointer-events: all;
  }
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  height: 100%;
  object-fit: cover;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
  -webkit-text-size-adjust: 100%;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

html {
  overflow-wrap: break-word;
}

.header {
  position: fixed;
  z-index: 100;
  top: 2.5rem;
  right: 0;
  left: 0;
  width: 100%;
}

.header__inner {
  width: 100%;
  max-width: 72.625rem;
  margin: 0 auto;
  padding: 0 var(--padding-pc);
}

.header__nav {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.875rem;
  line-height: 1.2;
  padding: 1.5rem 5rem;
  border-radius: 62.5rem;
  box-shadow: 0px 0.25rem 0.9375rem 0px rgba(37, 123, 6, 0.27);
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(0.3125rem);
  width: 100%;
}
@media screen and (max-width: 767px) {
  .header__nav {
    gap: 2rem;
    padding: 1rem 2.5rem;
  }
}

.header__list {
  display: flex;
  align-items: center;
  gap: 4rem;
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
}
@media screen and (max-width: 767px) {
  .header__list {
    gap: 2rem;
    flex-wrap: wrap;
  }
}

.header__item {
  flex-shrink: 0;
}

.header__link {
  display: block;
  font-size: 1.125rem;
  font-weight: var(--regular);
  font-family: "Zen Old Mincho", serif;
  color: #293707;
  line-height: 1.2;
  text-decoration: none;
  white-space: nowrap;
}
@media screen and (max-width: 767px) {
  .header__link {
    font-size: 1rem;
  }
}

.inner {
  width: 100%;
  max-width: calc(1920px + var(--padding-pc) * 2);
  padding-right: var(--padding-pc);
  padding-left: var(--padding-pc);
  margin-right: auto;
  margin-left: auto;
}
@media screen and (max-width: 767px) {
  .inner {
    max-width: calc(1920px + var(--padding-sp) * 2);
    padding-right: var(--padding-sp);
    padding-left: var(--padding-sp);
  }
}

.mv {
  overflow: hidden;
  aspect-ratio: 1920/1340;
  position: relative;
  background-image: url("../images/common/mv-bg.jpg.webp");
  background-repeat: no-repeat;
  background-size: cover;
}

.mv__dec {
  position: absolute;
  top: -1.25rem;
  left: -1.25rem;
  width: calc(100% + 2.5rem);
  height: calc(100% + 2.5rem);
  z-index: 1;
  pointer-events: none;
}

.mv__imgs {
  margin-top: -6rem;
  width: 62.5rem;
  height: 62.5rem;
  margin-inline: auto;
  position: relative;
  z-index: 2;
}

.mv__main-img img {
  border-radius: 50%;
}

.mv__title {
  position: absolute;
  left: 50%;
  top: 16.875rem;
  transform: translateX(-50%);
  aspect-ratio: 1;
  width: 15rem;
  border-radius: 50%;
  display: grid;
  place-content: center;
  background-color: var(--green);
  padding: 1.875rem 0.875rem;
}

.mv__title-icon {
  width: 8.125rem;
  aspect-ratio: 130/113;
  margin-inline: auto;
}

.mv__title-icon img {
  object-fit: contain;
  object-position: center;
}

.mv__title-text {
  text-align: center;
  margin-top: -0.3125rem;
  font-size: 2.25rem;
  font-weight: var(--regular);
  font-family: var(--darumadrop-one);
  color: var(--white);
  line-height: 1.4;
}

.mv__text {
  position: absolute;
  top: 13.75rem;
  left: 50%;
  transform: translateX(-51%);
  aspect-ratio: 913/458;
  width: 57.0625rem;
}

.mv__text img {
  object-fit: contain;
  object-position: center;
}

.mv__slider-wrap {
  position: absolute;
  bottom: 25rem;
  left: 0;
  right: 0;
  width: 100%;
  z-index: 3;
}

.mv__slider {
  border-bottom: 1px solid var(--white);
  display: flex;
  gap: 1.3125rem;
  width: fit-content;
}

.mv__slider:first-child {
  border-top: 1px solid var(--white);
}

.mv__slider-item {
  display: flex;
  align-items: center;
  gap: 1.3125rem;
  width: fit-content;
}

.mv__slider-text {
  font-family: var(--second-font-family);
  font-size: 5.0625rem;
  line-height: 1.5;
  color: var(--white);
  white-space: nowrap;
}

.mv__slider-img {
  border-radius: 6.25rem;
  aspect-ratio: 150/80;
  overflow: hidden;
  width: 9.375rem;
}

/* はじめましてセクション */
.intro {
  position: relative;
  width: 100%;
  padding: 6.25rem 0;
  background-color: #f3f2e9;
  z-index: 5;
  min-height: 75rem;
}

.intro__inner {
  max-width: 75rem;
  margin: 0 auto;
  padding: 0 var(--padding-pc);
  text-align: center;
}

.intro__title {
  font-family: var(--darumadrop-one);
  font-size: 3rem;
  line-height: 1.5;
  color: #6d8e1b;
  margin: 0 0 2.5rem 0;
  font-weight: var(--regular);
}

.intro__text {
  font-family: var(--base-font-family);
  font-size: 1.5rem;
  line-height: 2.1;
  color: #282828;
}
.intro__text p {
  margin: 0;
  white-space: pre-wrap;
}